import { SNACK_MESSAGES, SNACK_VARIANT } from "shared/Constants";

import { useGoogleLogin } from "@react-oauth/google";
import useNotification from "hooks/Notifications";
import { useState } from "react";

export const useGoogle = () => {
    const [googleResponse, setGoogleResponse] = useState({});
    const [googleError, setGoogleError] = useState({});
    const [msg, sendNotification] = useNotification();
    const GoogleSignin = useGoogleLogin({
        onSuccess: (tokenResponse) => setGoogleResponse(tokenResponse),
        onError: (error) => {
            setGoogleError(error);
            sendNotification({
                msg: SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
                variant: SNACK_VARIANT.ERROR,
            });
        },
    });
    return { googleResponse, googleError, GoogleSignin };
};
