import { FROM_VERIFICATION_CODE, LOGIN, SET_DATA, START_LOADING, STOP_LOADING } from "../Actions/Auth";

import { ACTION_STATES } from "../Actions/ActionStates";

const initialState = {
  loading: false,
  fromVerficationCode: false,
  profileData:{}
};
const commonReducer = (state = initialState, action) => {
  switch (action.type) {
    case START_LOADING: {
      return {
        ...state,
        loading: true,
      };
    }
    case STOP_LOADING: {
      return {
        ...state,
        loading: false,
      };
    }
    case FROM_VERIFICATION_CODE: {
      return {
        ...state,
        fromVerficationCode: action.value,
      };
    }
    case SET_DATA: {
      return {
        ...state,
        profileData: {...action.profileData},
      };
    }
    default: {
      return state;
    }
  }
};

export default commonReducer;
