import CustomForm from "components/CustomForm";
import { Images } from "shared/Images";
import {
    createRegistrationPayload,
    createUploadPayload,
    DOCUMENT_TYPE,
    DOCUMENT_TYPE_NAME,
    getDefaultValues,
    REGISTRATION_DETAILS_DATA,
} from "views/authenticated/constants";
import React, { useEffect, useRef, useState } from "react";
import { useDispatch } from "react-redux";

import CustomButton from "components/CustomButton";
import { INPUT_TYPES, LABEL, SNACK_VARIANT, STRINGS } from "shared/Constants";
import "./styles.scss";
import Header from "components/Header";
import { HEADER_DATA } from "views/HomePage/constants";
import Footer from "components/Footer/Footer";
import { updateProfile, uploadDocuments } from "redux/Actions/Auth";
import useNotification from "hooks/Notifications";
import { getDocsUrl } from "shared/Utilities";

function RegistrationDetails({ userDetails }) {
    const [phone, setPhone] = useState(userDetails?.phone_number ?? "");
    const [image, setImage] = useState(null);
    const [country, setCountry] = useState(userDetails?.address_details?.country ?? "");
    const [region, setRegion] = useState(userDetails?.address_details?.country ?? "");
    const [regionError, setRegionError] = useState(false);
    const [countryError, setCountryError] = useState(false);
    const imageRef = useRef(null);
    const dispatch = useDispatch();
    const [phoneNumberError, setPhoneNumberError] = useState(false);
    const [msg, sendNotifications] = useNotification();
    const [defaultValues, setDefaultValues] = useState({});
    console.log(userDetails, "user details");
    const onSubmit = (registrationData) => {
        if (phone.length > 4) {
            setPhoneNumberError(false);
            const { identity_documents, formData: payload } = createRegistrationPayload(
                registrationData,
                country,
                region,
                phone,
                image,
            );
            const documentPayload = createUploadPayload(
                identity_documents,
                DOCUMENT_TYPE_NAME.IDENTITY,
                true,
            );
            dispatch(
                updateProfile(
                    payload,
                    (response) => {
                        sendNotifications({
                            msg: response.message,
                            variant: SNACK_VARIANT.SUCCESS,
                        });
                    },
                    (error) => {
                        sendNotifications({
                            msg:
                                error.response?.data?.detail ??
                                error.response?.data?.message ??
                                error.message,
                            variant: SNACK_VARIANT.ERROR,
                        });
                    },
                ),
            );
            dispatch(
                uploadDocuments(
                    documentPayload,
                    (response) => {
                        sendNotifications({
                            msg: response.message,
                            variant: SNACK_VARIANT.SUCCESS,
                        });
                    },
                    (error) => {
                        sendNotifications({
                            msg:
                                error.response?.data?.detail ??
                                error.response?.data?.message ??
                                error.message,
                            variant: SNACK_VARIANT.ERROR,
                        });
                    },
                ),
            );
        } else {
            setPhoneNumberError(true);
        }
    };
    const onPhoneNumberChange = (data) => {
        if (data.length <= 4) {
            setPhoneNumberError(true);
        } else {
            setPhoneNumberError(false);
            setPhone(data);
        }
    };
    const handleImage = (event) => {
        setImage(URL.createObjectURL(event.target.files[0]));
    };
    const selectCountry = (val) => {
        setCountry(val);
    };
    const selectRegion = (val) => {
        setRegion(val);
    };
    // useEffect(() => {
    //     console.log("use effect log ==== ");
    // //    setDefaultValues()
    // },[userDetails])
    return (
        <div className="tab_content">
            <div className="profile_pic text-center mb-4 pb-3">
                <div className="avtar avtar200">
                    <img height={"100%"} width={"100%"} src={image} alt="" />
                    <input
                        id="selectImage"
                        hidden
                        type="file"
                        ref={imageRef}
                        accept="image/*"
                        onChange={handleImage}
                    />
                </div>
                {/* <input type="file" name="myImage" accept="image/*" /> */}
                <button id="plus" onClick={(e) => imageRef.current.click()} className="upload_file">
                    <img height={"100%"} width={"100%"} src={Images.cameraIcon} />
                </button>
            </div>
            <CustomForm
                id={"profile-verification"}
                onSubmit={onSubmit}
                reCaptcha={false}
                formData={REGISTRATION_DETAILS_DATA}
                phone={phone}
                onPhoneNumberChange={onPhoneNumberChange}
                phoneNumberError={phoneNumberError}
                renderSubmitButton={false}
                selectCountry={selectCountry}
                selectRegion={selectRegion}
                countryError={countryError}
                regionError={regionError}
                region={region}
                country={country}
                defaultValues={{ ...getDefaultValues(userDetails) }}
            />

            <div className="text-center">
                <CustomButton
                    className="btn btn-md btn-primary"
                    type={INPUT_TYPES.SUBMIT}
                    form="profile-verification"
                    title={STRINGS.SAVE}
                />
            </div>
        </div>
    );
}

export default React.memo(RegistrationDetails);
