import "./styles.scss";

import {
    ERROR_MESSAGES,
    ERROR_TYPES_GENERATION,
    INPUT_TYPES,
    LABEL,
    PASSWORD_TYPES,
    STRINGS,
    THEME,
} from "shared/Constants";
import React, { useEffect, useRef, useState } from "react";
import { getMaxDate, minDate, typeSelection, validateConfirmPassword } from "shared/Utilities";
import PhoneInput from "react-phone-input-2";
import "react-phone-input-2/lib/style.css";
import CustomButton from "components/CustomButton";
import CustomTextInput from "components/CustomTextInput";
import { Images } from "shared/Images";
import ReCAPTCHA from "react-google-recaptcha";
import { useForm } from "react-hook-form";
import { CountryDropdown, RegionDropdown, CountryRegionData } from "react-country-region-selector";
const CustomForm = ({
    onSubmit = () => { },
    onRecaptchaChange = () => { },
    renderSubmitButton = true,
    id = "hook-form",
    onAsyncScriptLoad = () => { },
    formData = [],
    privacyPolicy = false,
    selectRegion = () => { },
    country = "",
    region = "",
    selectCountry = () => { },
    onFileChange = () => { },
    jobAlert = false,
    reCaptcha = true,
    captchaError = false,
    phone = "",
    phoneNumberError = false,
    defaultValues = {},
    onPhoneNumberChange = () => { },
}) => {
    const [typeToggle, setTypeToggle] = useState({
        password: INPUT_TYPES.PASSWORD,
        [STRINGS.CONFIRM_PASSWORD]: INPUT_TYPES.PASSWORD,
    });
    const recaptchaRef = useRef(null);
    const {
        register,
        handleSubmit,
        watch,
        reset,
        formState,
        formState: { errors, isSubmitSuccessful },
    } = useForm({ defaultValues: { ...defaultValues } });
    const renderCaptcha = () => (
        <div className="mb-4">
            <ReCAPTCHA
                ref={recaptchaRef}
                sitekey={process.env.REACT_APP_RECAPTCHA_KEY}
                onChange={onRecaptchaChange}
                theme={THEME.LIGHT}
                asyncScriptOnLoad={onAsyncScriptLoad}
            />
            {captchaError && <div>{ERROR_MESSAGES.RECAPTCHA_ERROR}</div>}
        </div>
    );
    const policies = (registerValue, required, additionalInformation) => {
        return (
            <div className="mb-3">
                <div className="checkbox">
                    <input type="checkbox" {...register(registerValue, { required: required })} />
                    <label>{additionalInformation}</label>
                </div>
            </div>
        );
    };
    useEffect(() => {
        if (formState.isSubmitSuccessful) {
            reCaptcha && recaptchaRef.current.reset();
            // reset({ ...defaultValues });
        }
    }, [formState, reset]);
    useEffect(() => {
        Object.keys(defaultValues).length && reset({ ...defaultValues });
    }, [defaultValues]);
    return (
        <form id={id} onSubmit={handleSubmit(onSubmit)}>
            <div className="row">
                {formData.map((formItem, formIndex) => {
                    return (
                        <div
                            className="col-md-6 mb-4"
                            key={`${formItem.registerName} + ${formIndex}`}
                        >
                            {formItem.registerName === "phone_number" ? (
                                <div className="country_dropdown">
                                    <label>{LABEL.PHONE_NUMBER}</label>
                                    <PhoneInput
                                        inputProps={{
                                            name: "phone",
                                            // required: true,
                                        }}
                                        country={"us"}
                                        value={phone}
                                        onChange={onPhoneNumberChange}
                                        titleText={formItem.labelName}
                                    />
                                    {phoneNumberError && (
                                        <span className="error">{ERROR_MESSAGES.PHONE_NUMBER}</span>
                                    )}
                                </div>
                            ) : formItem.registerName === "country" ? (
                                <div className="position-relative">
                                    <label>{LABEL.COUNTRY}</label>
                                    <div className="country_picker">
                                        <CountryDropdown
                                            value={country}
                                            onChange={selectCountry}
                                            defaultOptionLabel={LABEL.SELECT}
                                        />
                                    </div>
                                </div>
                            ) : formItem.registerName === "state" ? (
                                <>
                                    <label>{LABEL.STATE}</label>
                                    <div className="country_picker">
                                        <RegionDropdown
                                            country={country}
                                            value={region}
                                            onChange={selectRegion}
                                            defaultOptionLabel={LABEL.SELECT}
                                            blankOptionLabel={LABEL.SELECT}
                                        />
                                    </div>
                                </>
                            ) : (
                                <>
                                    <CustomTextInput
                                        readOnly={!!formItem?.disabled}
                                        id={formItem.registerName}
                                        min={formItem.type === "date" ? minDate : ""}
                                        max={formItem.type === "date" ? getMaxDate() : ""}
                                        accept={
                                            formItem.type === "file"
                                                ? "application/pdf,application/vnd.ms-excel"
                                                : "*"
                                        }
                                        type={
                                            PASSWORD_TYPES.includes(formItem.registerName)
                                                ? typeToggle[formItem.registerName]
                                                : formItem.type
                                        }
                                        icon={
                                            PASSWORD_TYPES.includes(formItem.registerName)
                                                ? typeToggle[formItem.registerName] ===
                                                    INPUT_TYPES.PASSWORD
                                                    ? Images.HidePassword
                                                    : Images.ShowPassword
                                                : null
                                        }
                                        onIconClick={(data) => typeSelection(data, setTypeToggle)}
                                        iconStyle={"formIcon"}
                                        titleText={formItem.labelName}
                                        {...register(formItem.registerName, {
                                            pattern: formItem.pattern,
                                            required: formItem.required,
                                            validate: (val) =>
                                                validateConfirmPassword(
                                                    val,
                                                    formItem.registerName,
                                                    watch,
                                                ),
                                        })}
                                    />
                                    {ERROR_TYPES_GENERATION.includes(
                                        errors[formItem.registerName]?.type,
                                    ) && (
                                            <span className="error">
                                                {
                                                    formItem.errorMessages[
                                                    errors[formItem.registerName]?.type
                                                    ]
                                                }
                                            </span>
                                        )}
                                    {/* </div> */}
                                </>
                            )}
                        </div>
                    );
                })}
                {privacyPolicy && (
                    <>
                        {policies(STRINGS.PRIVACY_POLICY_REGISTER, true, STRINGS.PRIVACY_POLICY)}
                        {errors[STRINGS.PRIVACY_POLICY_REGISTER]?.type === "required" ? (
                            <span className="error">{ERROR_MESSAGES.PRIVACY_POLICY}</span>
                        ) : null}
                    </>
                )}
                {jobAlert && policies(STRINGS.JOB_ALERT_REGISTER, false, STRINGS.JOB_ALERT)}
                {reCaptcha && renderCaptcha()}
                {renderSubmitButton && (
                    <div className="text-center mb-4">
                        <CustomButton
                            className="btn btn-md btn-primary"
                            type="submit"
                            title={STRINGS.SAVE}
                        />
                    </div>
                )}
            </div>
        </form>
    );
};

export default React.memo(CustomForm);
